import { addDoc, collection } from "firebase/firestore";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";


async function addProduct(db, product) {
    try {
        await addDoc(collection(db, "products"), {
            ...product
        });
        console.log(`${product.name} product was succesfully added! :)`)
    } catch(e) {
        console.log(e);
    }

}

const imageUploadedReference = async (storage, file) => {
    const storageRef = ref(storage, `products/images/${file.name}`);
    return await uploadBytes(storageRef, file);
};

const uploadImages = async (storage, files) => {
    console.log("Uploading product images...");
    const images = files.map(async (file) => {
        const image = await imageUploadedReference(storage, file);
        return getDownloadURL(ref(storage, image.ref.fullPath));
    });

    return images;

}

export {
    addProduct,
    uploadImages
};